import { emptyAdmin } from '../../../core/types/admin';
import { CrudComponent } from '../../../shared/crud';
export class AdminsComponent extends CrudComponent {
    constructor() {
        super(...arguments);
        this.users = [];
        this.admins = [];
        this.organizers = [];
        this.idField = "userId";
        this.admTypes = [
            {
                id: 0,
                name: 'Администратор'
            },
            {
                id: 1,
                name: 'Модератор'
            }
        ];
    }
    getEmptyEntity() {
        return emptyAdmin;
    }
    getEntities() {
        this.api.get("api/admin/users").subscribe(users => {
            this.users = users;
        });
        this.api.get("api/admin/organizers").subscribe(organizers => {
            this.organizers = organizers;
        });
        return this.api.get("api/admin/admins");
    }
    putEntity(id, data) {
        return this.api.put(`/api/admin/Admins/${id}`, data);
    }
    postEntity(data) {
        return this.api.post(`/api/admin/Admins`, data);
    }
    deleteEntity(id) {
        return this.api.delete(`/api/admin/Admins/${id}`);
    }
    getForm() {
        return this.fb.group(emptyAdmin);
    }
    find(id) {
        return this.entities.find(e => e.userId === +id) || this.getEmptyEntity();
    }
    isEqual(e1, e2) {
        return e1.userId === e2.userId;
    }
    formatAdminType(type) {
        if (type === 0) {
            return "Администратор";
        }
        return "Модератор";
    }
}
