import { CrudComponent } from '../../../shared/crud';
import { emptyLexicon } from '../../../core/types/lexicon';
import { Phrase } from '../../../core/types/phrase';
export class LexiconComponent extends CrudComponent {
    constructor() {
        super(...arguments);
        this.languages = [];
        this.phrases = [];
        this.isEditPhraseVisible = false;
        this.phraseForm = this.fb.group({
            languageId: [],
            name: []
        });
    }
    getEmptyEntity() {
        return emptyLexicon;
    }
    getEntities() {
        this.api.get("api/admin/languages").subscribe(languages => {
            this.languages = languages;
        });
        return this.api.get("api/admin/lexicons");
    }
    putEntity(id, data) {
        return this.api.put(`/api/admin/lexicons/${id}`, data);
    }
    postEntity(data) {
        return this.api.post(`/api/admin/lexicons`, data);
    }
    deleteEntity(id) {
        return this.api.delete(`/api/admin/lexicons/${id}`);
    }
    getForm() {
        return this.fb.group({
            id: [],
            code: [],
            category: [],
            commentPhrase: []
        });
    }
    find(id) {
        return this.entities.find(e => e.id === +id) || this.getEmptyEntity();
    }
    isEqual(e1, e2) {
        return e1.id === e2.id;
    }
    editPhrases(id) {
        this.currentLexiconId = id;
        return this.api.get(`api/admin/phrases/${id}`).subscribe(phrases => {
            this.phrases = phrases;
        });
    }
    appendPhrase() {
        this.isEditPhraseVisible = true;
        this.phraseForm.reset();
    }
    editPhrase(phrase) {
        this.currentPhraseId = phrase.id;
        this.phraseForm.patchValue(phrase);
        this.isEditPhraseVisible = true;
    }
    handleCancelPhrase() {
        this.isEditPhraseVisible = false;
    }
    handleOkPhrase() {
        if (this.currentPhraseId) {
            return this.api.put(`/api/admin/phrases/${this.currentPhraseId}`, this.phraseForm.value).subscribe(phrase => {
                this.phrases = this.phrases.map(p => {
                    if (p.id === phrase.id) {
                        return phrase;
                    }
                    else {
                        return p;
                    }
                });
                this.isEditPhraseVisible = false;
                this.currentPhraseId = undefined;
            });
        }
        else {
            return this.api.post(`/api/admin/phrases/${this.currentLexiconId}`, this.phraseForm.value).subscribe(phrase => {
                this.phrases = this.phrases.concat(phrase);
                this.isEditPhraseVisible = false;
                this.currentPhraseId = undefined;
            }, ({ error }) => {
            });
        }
    }
    removePhrase(id) {
        return this.api.delete(`/api/admin/phrases/${id}`).subscribe(() => {
            this.phrases = this.phrases.filter(p => p.id !== id);
        });
    }
}
