import { AwardType } from '../../../core/types/awardType';
import { CrudComponent } from '../../../shared/crud';
export class AwardTypesComponent extends CrudComponent {
    constructor() {
        super(...arguments);
        this.fileList = [];
        this.isImageVisible = false;
        this.currentImage = '';
        this.beforeUpload = (file) => {
            this.fileList = [file];
            return false;
        };
    }
    /* getEmptyEntity() { */
    /*   return emptyAwardType; */
    /* } */
    getEntities() {
        return this.api.get('api/admin/awardTypes');
    }
    putEntity(id, data) {
        const formData = new FormData();
        formData.append('name', data.name);
        formData.append('img', this.fileList[0]);
        return this.api.put(`/api/admin/awardTypes/${id}`, formData);
    }
    postEntity(data) {
        const formData = new FormData();
        formData.append('name', data.name);
        formData.append('img', this.fileList[0]);
        return this.api.post(`/api/admin/awardTypes`, formData);
    }
    deleteEntity(id) {
        return this.api.delete(`/api/admin/awardTypes/${id}`);
    }
    getForm() {
        return this.fb.group({
            id: [],
            name: [null, []],
            img: [null, []]
        });
    }
    find(id) {
        return this.entities.find(e => e.id === +id) || this.getEmptyEntity();
    }
    isEqual(e1, e2) {
        return e1.id === e2.id;
    }
    showImage(image) {
        this.currentImage = image;
        this.isImageVisible = true;
    }
    afterPost() {
        this.fileList = [];
    }
    afterPut() {
        this.fileList = [];
    }
}
