import { emptyOrganizer } from '../../../core/types/organizer';
import { Language } from '../../../core/types/language';
import { CrudComponent } from '../../../shared/crud';
export class OrganizersComponent extends CrudComponent {
    constructor() {
        super(...arguments);
        this.languages = [];
    }
    getEmptyEntity() {
        return emptyOrganizer;
    }
    getEntities() {
        this.api.get("api/admin/languages").subscribe(languages => {
            this.languages = languages;
        });
        return this.api.get("api/admin/Organizers");
    }
    putEntity(id, data) {
        return this.api.put(`/api/admin/Organizers/${id}`, data);
    }
    postEntity(data) {
        return this.api.post(`/api/admin/Organizers`, data);
    }
    deleteEntity(id) {
        return this.api.delete(`/api/admin/Organizers/${id}`);
    }
    getForm() {
        return this.fb.group(emptyOrganizer);
    }
    find(id) {
        return this.entities.find(e => e.id === +id) || this.getEmptyEntity();
    }
    isEqual(e1, e2) {
        return e1.id === e2.id;
    }
}
