import { OnChanges } from '@angular/core';
import { CrudComponent } from 'src/app/shared/crud';
import { of } from 'rxjs';
export class ContestAwardStackComponent extends CrudComponent {
    constructor() {
        super(...arguments);
        this.contestId = -1;
        this.awardTypes = [];
    }
    ngOnChanges() {
        if (this.contestId > 0) {
            this.loadAwardTypes();
            this.loadStacks();
        }
    }
    loadStacks() {
        this.api
            .get(`api/admin/awardStacks/${this.contestId}`)
            .subscribe(entities => {
            this.entities = entities;
        });
    }
    loadAwardTypes() {
        this.api
            .get('api/admin/awardTypes')
            .subscribe(awardTypes => {
            this.awardTypes = awardTypes;
        });
    }
    getEntities() {
        return of([]);
    }
    putEntity(id, data) {
        return this.api.put(`/api/admin/awardStacks/${id}`, data);
    }
    postEntity(data) {
        return this.api.post(`/api/admin/awardStacks/${this.contestId}`, data);
    }
    deleteEntity(id) {
        return this.api.delete(`/api/admin/awardStacks/${id}`);
    }
    getForm() {
        return this.fb.group({
            id: [],
            awardTypeId: [null, []],
            position: [null, []],
            countAwards: [null, []]
        });
    }
    find(id) {
        return this.entities.find(e => e.id === +id) || this.getEmptyEntity();
    }
    isEqual(e1, e2) {
        return e1.id === e2.id;
    }
}
